 "use client";
import Link from "next/link";
import { useState, useEffect } from "react";
import Profile from "../components/Profile"; // Ensure correct path
import { auth } from "../lib/firebase";
import { onAuthStateChanged, signOut } from "firebase/auth";
import AuthModal from "../components/AuthModal"; // Ensure correct path
import PropTypes from 'pro-types';
const Navbar = ({setShowProfile}) => {
  
  const [user, setUser] = useState(null);
  const [isAuthModalOpen, setIsAuthModalOpen] = useState(false);

  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, (currentUser) => {
      setUser(currentUser);
    });
    return () => unsubscribe();
  }, []);

  const handleLogout = async () => {
    await signOut(auth);
    setShowProfile(false); // Ensure profile menu closes on logout
  };

  console.log("âœ… Navbar Rendered. setShowProfile exists:", typeof setShowProfile); // Debugging log

  return (
    <>
      <nav className="flex justify-between items-center bg-gray-100 p-4">
        {/* Brand Logo */}
        <Link href="/" className="text-xl font-bold">Home</Link>

        <div className="relative flex items-center space-x-4">
          {user ? (
            <>
              {/* Toggle Profile */}
              <button onClick={() => setShowProfile(true)} className="flex items-center">
                <img
                  src={user.photoURL || "https://tecdn.b-cdn.net/img/new/avatars/2.jpg"}
                  className="rounded-full"
                  style={{ height: "30px", width: "30px" }}
                  alt="Profile"
                  loading="lazy"
                />
              </button>

              {/* Debugging Log */}
              {console.log("ðŸ”¹ Passing setShowProfile to Profile:", typeof setShowProfile)}

              {/* Show Profile Component and Pass setShowProfile */}
              {showProfile && <Profile setShowProfile={setShowProfile} />}
            </>
          ) : (
            <button
              onClick={() => setIsAuthModalOpen(true)}
              className="px-4 py-2 bg-blue-600 text-white rounded-md transition hover:bg-blue-700"
            >
              Sign In / Register
            </button>
          )}
        </div>
      </nav>

      {/* Include AuthModal */}
      <AuthModal isOpen={isAuthModalOpen} onClose={() => setIsAuthModalOpen(false)} />
    </>
  );
};

Navbar.propTypes = {
  setShowProfile: PropTypes.func.isRequired, // Validate that `setShowLogin` is a function
};

export default Navbar;
